import { Injectable } from '@angular/core';
import { BehaviorSubject } from 'rxjs';

@Injectable({
  providedIn: 'root'
})
export class LoadingService {

  isLoading: boolean;
  private loadingSbj = new BehaviorSubject<boolean>(this.isLoading);
  public loading$ = this.loadingSbj.asObservable();

  constructor() { }

  start(){
    this.isLoading = true;
    this.loadingSbj.next(this.isLoading);
  }

  end(){
    this.isLoading = false;
    this.loadingSbj.next(this.isLoading);
  }
}
